.price {
	&__title {
		font-size: 20px;
		padding: 25px 0 10px 0;
	}
	@media (min-width: $screen-sm) {
		height: 333px;
		background-color: $light-grey;
	}
	@media (min-width: $screen-lg) {
		background-color: #FFF;
		height: 537px;
		&__title {
			font-size: 30px;
			padding: 85px 0 10px 0;

		}
	}

	&__inner {
		@media (min-width: $screen-xs) {
 			width: 300px;
 			margin:auto;
 			&:after {
				content: '';
				clear: both;
				display: table;
			}
 		}
		@media (min-width: $screen-sm) {
			width: 746px;
		}
		@media (min-width: $screen-lg) {
			width: 1170px;
		}
		
	}
}